import React from 'react';
import HeaderSection from './Header';
import Footer from '../components/Footer';
const SingleImageCard = (props) => (
  <div className="single-image-card">
    <img src={props.src} />
  </div>
)
const DoubleImageCard = (props) => (
  <div className="double-image-card">
    <img src={props.src} className="clients-doublecard-image" />
    <img src={props.src1} />
  </div>
)
const ClientsPage = () => (
  <main className="clients-page">
    <HeaderSection />
    <div className="clients-innerimage-section">
      <h2 className="clients-heading-content">Clients</h2>
    </div>
    <div className="clients-card-container">
      <div className="clients-image-container">
        <SingleImageCard src="./public/images/c4.png" />
        <DoubleImageCard src="./public/images/c16.png" src1="./public/images/c12.png" />
        <DoubleImageCard src="./public/images/c10.png" src1="./public/images/c19.png" />
        <SingleImageCard src="./public/images/c9.png" />
      </div>
      <div className="clients-innerimage-container">
        <DoubleImageCard src="./public/images/c13.png" src1="./public/images/c14.png" />
        <SingleImageCard src="./public/images/c6.png" />
        <DoubleImageCard src="./public/images/c18.png" src1="./public/images/c20.png" />
        <SingleImageCard src="./public/images/c8.png" />
      </div>
      <div className="clients-innerimage-container">
        <SingleImageCard src="./public/images/c11.png" />
        <DoubleImageCard src="./public/images/c15.png" src1="./public/images/c17.png" />
        <SingleImageCard src="./public/images/c5.png" />
        <SingleImageCard src="./public/images/c7.png" />
      </div>
    </div>
    <Footer />
  </main>
);
export default ClientsPage;



