import React from 'react';
import {render} from 'react-dom';
const Card = (props) => (
  <div>
    <img src={props.src} className="team-card-image"/>
    <span className="team-name-section">{props.name}</span>
  </div>
)
const OurTeams = (props) => (
  <section>
    <div className="team-image-section">
      <h1 className="team-heading">OurTeam</h1>
    </div>
    <div className="team-section-content">
      <Card src="./public/images/TeamChachan1.png" name='CA. Naresh Kumar Lath' />
      <Card src="./public/images/TeamChachan2.png" name='CA. Dharmendra Kumar Chachan' />
      <Card src="./public/images/TeamChachan3.png" name='CA. Ranjeet Kumar Tibrewal' />
    </div>
  </section>
);
export default OurTeams;



