import React from 'react';
import HeaderSection from './Header';
import Footer from '../components/Footer';
const ContactPage = () => (
  <main className="services-page">
    <HeaderSection />
    <section>
      <div className="contacts-inner-image">
        <h1 className="contacts-heading">Contact Us</h1>
      </div>
      <div className="contacts-inner-section">
        <div className="contacts-inner-content">
          <span className="contacts-inner-heading">Head Office</span><br />
          <span className="contacts-inner-text">1308- 09, Best SkyTower,<br />F-5,
            Netaji Subhash Place,<br />Delhi-110034, India</span><br />
          <span className="contacts-inner-text">+91 11 46461616<br />+91 11 46461617</span>
          <span className="contacts-inner-text">+91 9873288915<br />+91 9811088915</span><br />
          <span className="contacts-inner-text">info@chachanlath.in<br /></span>
          <span className="contacts-inner-heading">Branch Office</span><br />
          <span className="contacts-inner-text">B-5, 1 st Floor,<br />New Shopping Complex, Shivalik Nagar,<br />Haridwar-249403,Uttarakhand, India</span><br />
          <span className="contacts-inner-text">+91 13 34232154<br />+91 9358220758</span><br />
          <span className="contacts-inner-text">haridwar@chachanlath.in</span><br />
        </div>
        <div className="contacts-inner-container">
          <h3>Feedback/Query</h3>
          <input type="text" name="name" placeholder="Name" className="contacts-innerform-text" />
          <input type="email" name="email" placeholder="Email" className="contacts-innerform-text" />
          <input type="text" name="phoneNo" placeholder="Phone No" className="contacts-innerform-text" />
          <textarea className= "contacts-innermessage-section" placeholder="Type your Message here."></textarea>
          <input className="contacts-submit-button" type="submit" value="Submit" />
        </div>
      </div>
      <Footer />
    </section>
  </main>
);
export default ContactPage;



