import React from 'react';
import {render} from 'react-dom';

const Card = (props) => (
  <div>
    <img src={props.src} className="clients-card-image" />
  </div>
)
const Clients = (props) => (
  <section>
    <div className="clients-image-section">
      <h1 className="clients-heading">Clients</h1>
    </div>
    <div className="clients-section-content">
      <Card src="./public/images/c1.png" />
      <Card src="./public/images/c2.png" />
      <Card src="./public/images/c3.png" />
    </div>
  </section>
);
export default Clients;



