import React from 'react';
import {render} from 'react-dom';

const Card = (props) => (
  <div className="services-border">
    <img src={props.src} className="services-image-block" />
    <span dangerouslySetInnerHTML={{__html: props.name}} className="services-text"></span>
  </div>
)
const Services = () => (
  <section>
    <div className="services-image-section">
      <h1 className="services-heading">Services</h1>
    </div>
    <div className="services-section">
      <div className="services-section-content">
        <Card src="./public/images/services1.png" name='<br />Transaction Advisory' />
        <Card src="./public/images/services2.png" name='Audits'/>
      </div>
      <div className="services-section-content">
        <Card src="./public/images/services3.png" name='Outsourcing' />
        <Card src="./public/images/services4.png" name='Company Law <br/> & Related Matters' />
      </div>
    </div>
    <div className="services-section">
      <div className="services-section-content">
        <Card src="./public/images/services5.png" name='Direct Taxes Planning<br />& Compliance' />
        <Card src="./public/images/services6.png" name='Indirect Taxes Advisory &<br />Compliance (GST, Excise Duty,<br />Service Tax, VAT)<br />' />
      </div>
      <div className="services-section-content">
        <Card src="./public/images/services7.png" name='Legal Assistance' />
        <Card src="./public/images/services8.png" name='Overseas Direct Investment<br />& Foreign Direct Investment' />
      </div>
    </div>
  </section>
);
export default Services;



