import React from 'react';
import {render} from 'react-dom';

const Contacts = (props) => (
  <section className="contacts-image-section">
    <div className="contacts-section-content">
      <div className="contacts-section-container">
        <div className="contacts-image-content">
          <img className="contacts-locationimage-section" src="./public/images/location.png" />
          <img className="contacts-phoneimage-section" src="./public/images/phone.png" />
          <img className="contacts-messageimage-section" src="./public/images/email.png" />
        </div>
        <div className="contacts-image-content">
          <span className="contacts-address-heading">Head Office</span><br />
          <span>1308- 09, Best SkyTower,<br />F-5,
            Netaji Subhash Place,<br />Delhi-110034, India</span>
          <span className="contacts-address-phonenumber">+91 11 46461616<br />+91 11 46461617</span>
          <span className="contacts-address-phonenumber">+91 9873288915<br />+91 9811088915</span>
          <span className="contacts-address-email">info@chachanlath.in<br /></span>
        </div>
      </div>
      <div className="contacts-section-container">
        <div className="contacts-image-content">
          <img className="contacts-locationimage-section" src="./public/images/location.png" />
          <img className="contacts-phoneimage-section" src="./public/images/phone.png" />
          <img className="contacts-messageimage-section" src="./public/images/email.png" />
        </div>
        <div>
          <span className="contacts-address-heading">Branch Office</span><br />
          <span>B-5, 1 st Floor,<br />New Shopping Complex, Shivalik Nagar,<br />Haridwar-249403,Uttarakhand, India</span>
          <span className="contacts-address-phonenumber">+91 13 34232154<br />+91 9358220758</span>
          <span className="contacts-branch-email">haridwar@chachanlath.in</span>
        </div>
      </div>
      <div className="contacts-container">
        <span className="contacts-address-heading">Feedback/Query</span>
        <input type="text" name="name" placeholder="Name" className="contacts-form-text" />
        <input type="email" name="email" placeholder="Email" className="contacts-form-text" />
        <input type="text" name="phone" placeholder="Phone no" className="contacts-form-text" />
        <textarea className= "contacts-message-section" placeholder="Type your Message here."></textarea>
        <input className="contacts-submit-button" type="submit" value="Submit" />
      </div>
    </div>
  </section>
);
export default Contacts;



